const PINATA_JWT = "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..QvTuEpQKFRPgU8ZnZOnE6s-x5c-9GHXv5fcouxsxQnE"; // paste your Pinata token here
const PINATA_URL = "https://api.pinata.cloud/pinning/pinFileToIPFS";

export async function uploadToIPFS(file) {
  const formData = new FormData();
  formData.append("file", file);

  try {
    const res = await fetch(PINATA_URL, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${PINATA_JWT}`,
      },
      body: formData,
    });

    const data = await res.json();
    if (data.IpfsHash) {
      return data.IpfsHash; // CID
    } else {
      console.error("Upload failed:", data);
      return null;
    }
  } catch (error) {
    console.error("Pinata Upload Error:", error);
    return null;
  }
}

export async function fetchFromIPFS(cid) {
  try {
    const url = `https://gateway.pinata.cloud/ipfs/${cid}`;
    const res = await fetch(url);
    if (!res.ok) {
      console.error(`Failed to fetch ${cid} - [${res.status}] ${res.statusText}`);
      return null;
    }
    return await res.text();
  } catch (error) {
    console.error("Pinata Fetch Error:", error);
    return null;
  }
}
